.aside {
  margin: 20px 30px;
}

.content {
  margin-right: 350px;
}

.page {
  width: 400px;
  height: 50px;
  margin: 0 auto;
  padding-top: 20px;
}

@media all and (max-width:1200px) {
  .aside {
    display: none;
  }

  .content {
    margin: 0px auto;
    width: 100%;
    margin-right: 200px;
    margin-top: 20px;
    padding: 0 50px;
  }
}

